$(function() {
  $(document)
    .on('click', '.mobile_menu_list li:eq(1)', function(e) {
      e.preventDefault();
      e.stopPropagation();

      if ($(this).hasClass('active')) {
        $(this).removeClass('active');
        $('.menu_list').removeClass('active');
        $('body').removeClass('modal_open');
      }
      else {
        $(this).addClass('active');
        $('.menu_list').addClass('active');
        $('body').addClass('modal_open');
      }
    })
    .on('click', '.btn_ico_favorite, .btn_add_favorite', function(e) {
      e.preventDefault();
      e.stopPropagation();

      $('.favorite_modal').modal('show')
    })
    .on('click', '.btn_ico_cart, .btn_add_cart', function(e) {
      e.preventDefault();
      e.stopPropagation();

      $('.cart_modal').modal('show')
    })
    .on('click', '.btn_login', function(e) {
      e.preventDefault();
      e.stopPropagation();
      
      let isValid = true;

      $('.login_form input').each(function() {
        if ($(this).val().trim() == '') {
          $(this).addClass('is-invalid');
          isValid = false;
        }
        else {
          $(this).removeClass('is-invalid');
        }
      });

      if (isValid) {
        location.href = $(this).attr('href');
      }
    })
    .on('click', '.btn_modify_profile', function(e) {
      e.preventDefault();
      e.stopPropagation();

      $('.profile_form input').each(function() {
        if ($(this).val().trim() == '') {
          $(this).addClass('is-invalid');
        }
        else {
          $(this).removeClass('is-invalid');
        }
      });
    })
    .on('click', '.btn_modify_pwd', function(e) {
      e.preventDefault();
      e.stopPropagation();

      $('.password_form input').each(function() {
        if ($(this).val().trim() == '') {
          $(this).addClass('is-invalid');
        }
        else {
          $(this).removeClass('is-invalid');
        }
      });
    })
    .ready(function() {
      
    });
  
  $(window)
    .on('resize', function() {
      let intWindowWidth = $(window).width();

      if (intWindowWidth >= 768) {
        $('.mobile_menu_list li:eq(1)').removeClass('active');
        $('.menu_list').removeClass('active');
        $('body').removeClass('modal_open');
      }
    });
});